body{
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    background-color: #004643;
    color: #fffffe;
}
p{
    color: #c2e5db;
}
.card{
    width: 100px;
    height: 100px;
    border: 2px solid black;
    display: flex;
    justify-content: center;
}
#card-container-one.hidden, #card-container-two.hidden, #card-container-three.hidden, #card-container-four.hidden, #card-container-five.hidden{
    display: none;
}
.card img{
    width: 75px;
    height: 75px;
    align-self: center;
}
.card:hover{
    animation: cardAnim 1s ease forwards;
    cursor: pointer;
}
#card-container-one, #card-container-two, #card-container-three, #card-container-four, #card-container-five{
    display: grid;
    grid-auto-rows: 1fr;
    grid-template-columns: repeat(10, 100px);
    gap: 25px;
    width: 1225px;
    align-self: center;
    justify-self: center;
}
#card-container-one{
    width: 600px;
}
#card-container-two{
    width: 700px;
}
#card-container-three{
    width: 950px;
}
#card-container-four{
    width: 1225px;
}
#main{
    display: grid;
    justify-content: center;
}
header{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
h2{
    font-size: 48px;
}
p{
    font-size: 18px;
}
@keyframes cardAnim {
    from{
        scale: 1;
    }
    to{
        scale: 1.2;
    }
}